ias y del cliente) aportarán beneficios para ambas partes; seleccionar información que se relacione con los objetivos a solucionar y aportar las mejores herramientas para conseguirlo es necesario para crear relaciones más estrechas a lo largo de los proyectos. ")])])])]),_c('div',{staticClass:"tarjeta-avatar-b mb-5"},[_c('img',{attrs:{"src":require("@/assets/curso/icon-documento-1.svg"),"alt":"Icono relacionado "}}),_c('div',{staticClass:"tarjeta tarjeta--azul"},[_c('div',{staticClass:"p-4"},[_c('p',{staticClass:"mb-0"},[_vm._v("Incluir en las presentaciones cronogramas o líneas de tiempo donde se vea con claridad las metas que se alcanzarán en una cantidad de tiempo determinada, etapas más cruciales, entregables, etc., demostrarán una propuesta más organizada.")])])])]),_c('div',{staticClass:"tarjeta-avatar-b mb-5"},[_c('img',{attrs:{"src":require("@/assets/curso/icon-documento-1.svg"),"alt":"Icono relacionado "}}),_c('div',{staticClass:"tarjeta tarjeta--azul"},[_c('div',{staticClass:"p-4"},[_c('p',{staticClass:"mb-0"},[_vm._v("Otra herramienta a tener en cuenta es la posibilidad de entregar un elemento (documento, archivo, diseño o formato) que pueda ser entregado al cliente, esto ayudará a que estemos en la mente del cliente cuando no se esté presente y dará una buena impresión. Por obvias razones es importante no hacer entrega de archivos o diseños originales o ideas no registradas, o cualquier información que no pueda estar debidamente protegida, además de cuidar los archivos usando marcas de agua donde sea necesario.")])])])]),_c('div',{staticClass:"tarjeta-avatar-b mb-5"},[_c('img',{attrs:{"src":require("@/assets/curso/icon-documento-1.svg"),"alt":"Icono relacionado "}}),_c('div',{staticClass:"tarjeta tarjeta--azul"},[_c('div',{staticClass:"p-4"},[_c('p',{staticClass:"mb-0"},[_vm._v("Siempre que se hace una presentación cabe la posibilidad que las personas no entiendan conceptos, frases o ideas, por eso es necesario dar la posibilidad de responder a dudas o c